<template>
  <div class="PostList">
    <div class="loading" v-if="isLoading">
      <img src="../assets/loading.gif" />
    </div>
    <div class="posts" v-else>
      <ul>
        <li>
          <div class="toobar">
            <span
              @click="judgeType('all')"
              :class="type === 'all' && 'selected'"
              >全部</span
            >
            <span
              @click="judgeType('good')"
              :class="type === 'good' && 'selected'"
              >精华</span
            >
            <span
              @click="judgeType('share')"
              :class="type === 'share' && 'selected'"
              >分享</span
            >
            <span
              @click="judgeType('ask')"
              :class="type === 'ask' && 'selected'"
              >问答</span
            >
            <span
              @click="judgeType('job')"
              :class="type === 'job' && 'selected'"
              >招聘</span
            >
          </div>
        </li>
        <li v-for="(post, index) in posts" :key="index">
          <!-- 头像 -->

          <router-link
            :to="{
              name: 'user_info',
              params: {
                name: post.author.loginname,
              },
            }"
            ><img :src="post.author.avatar_url" />
          </router-link>
          <!-- 回复/浏览 -->
          <span>
            <span class="reply_count">{{ post.reply_count }}</span>
            /{{ post.visit_count }}
          </span>
          <!-- 帖子分类 -->
          <span
            :class="[
              {
                put_good: post.good == true,
                put_top: post.top == true,
                'topiclist-tab': post.good != true && post.top != true,
              },
            ]"
          >
            <span>
              {{ post | tabFormatter }}
            </span>
          </span>
          <!-- 标题 -->
          <router-link
            :to="{
              name: 'post_content',
              params: {
                id: post.id,
                name: post.author.loginname,
              },
            }"
          >
            <span>
              {{ post.title }}
            </span>
          </router-link>

          <!--  最终回复时间 -->
          <span class="last_reply">
            {{ post.last_reply_at | formatDate }}
          </span>
        </li>
        <li>
          <pagination @handleList="renderList"></pagination>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import pagination from "./Pagination.vue";
export default {
  data() {
    return {
      isLoading: false,
      posts: [],
      postpage: 1,
      type: "all",
    };
  },
  components: {
    pagination,
  },
  methods: {
    judgeType(value) {
      this.type = value;
      this.getData();
      /* console.log(type.target.className); */
    },
    getData() {
      this.$http
        .get("https://cnodejs.org/api/v1/topics", {
          params: {
            tab: this.type,
            page: this.postpage,
            limit: 20,
          },
        })
        .then((res) => {
          this.isLoading = false;
          this.posts = res.data.data;
        })
        .catch(function (err) {
          console.log(err);
        });
    },
    renderList(value) {
      this.postpage = value;
      this.getData();
    },
  },
  beforeMount() {
    this.isLoading = true;
    this.getData();
  },
};
</script>

<style  scoped>
.PostList .selected {
  background-color: rgb(128, 181, 1);
  color: white;
  padding: 2.5px;
  border-radius: 3px;
}
.PostList {
  background-color: #e1e1e1;
}
.posts {
  margin-top: 10px;
}

.PostList img {
  height: 30px;
  width: 30px;
  vertical-align: middle;
}

ul {
  list-style: none;
  width: 100%;
  max-width: 1344px;
  margin: 0 auto;
}

ul li:not(:first-child) {
  padding: 9px;
  font-size: 15px;
  font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma,
    "Hiragino Sans GB", STHeiti, sans-serif !important;
  font-weight: 400;
  background-color: white;
  color: #333;
  border-top: 1px solid #f0f0f0;
}

li:not(:first-child):hover {
  background: #f5f5f5;
}

li:last-child:hover {
  background: white;
}

li span {
  line-height: 30px;
}

.allcount {
  width: 70px;
  display: inline-block;
  text-align: center;
  font-size: 12px;
}

.reply_count {
  color: #9e78c0;
  font-size: 14px;
}

.put_good,
.put_top {
  background: #80bd01;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  color: #fff;
  font-size: 12px;
  margin-right: 10px;
}

.topiclist-tab {
  background-color: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  font-size: 12px;
  margin-right: 10px;
}

.last_reply {
  text-align: right;
  min-width: 50px;
  display: inline-block;
  white-space: nowrap;
  float: right;
  color: #778087;
  font-size: 12px;
}

.toobar {
  height: 40px;
  background-color: #f5f5f5;
}

.toobar span {
  font-size: 14px;
  color: #80bd01;
  line-height: 40px;
  margin: 0 10px;
  cursor: pointer;
}

.toobar span:hover {
  color: #9e78c0;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  text-decoration: underline;
}

.loading {
  text-align: center;
  padding-top: 300px;
}
</style>


